div {
  color: white;
}
.wrapper2 {
  color: white;
  width: 400px;
  height: 300px;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(50px);
  border-radius: 10px;
  padding: 40px 35px 55px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(
    -50%,
    -50%
  ); /* Center the div horizontally and vertically */
  text-align: center;
}
.wrapper2 h1 {
  margin-top: -20px;
}
.lol {
  margin: 0;
  margin-left: -35px;
  margin-top: -12px;
}
.wrapper2 .btn {
  width: 200px;
  height: 45px;
  background: #fff;
  border: none;
  outline: none;
  border-radius: 6px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  font-size: 16px;
  color: #333;
  font-weight: 600;
  margin-top: 10px;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  letter-spacing: 4px;
}
.wrapper2 .btn:hover {
  background: #0a8181;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px #c3c8c9, 0 0 25px #d6d9da, 0 0 50px #e3e6e6,
    0 0 100px #d2d6d6;
}
.wrapper2 form {
}

.wrapper2 .input-box .input-field input {
  width: 300px;
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.2);
  outline: none;
  font-size: 16px;
  color: #fff;
  border-radius: 6px;
  padding: 15px 15px 15px 40px;
}
.linkToSignUpDiv {
  display: flex;
  justify-content: space-between;
}
h3 {
  font-size: 16px;
}
a {
  position: absolute;
  margin-left: 170px;
}

@media screen and (max-width: 576px) {
  .wrapper2 {
    width: 300px;
    height: 280px;
  }
  .wrapper2 .input-box .input-field input {
    width: 200px;
    height: 40px;
  }
  .wrapper2 .icon {
    margin-top: -5px;
    margin-left: 6px;
  }
  .wrapper2 h3 {
    font-size: 13px;
    margin-left: 34px;
  }
  .link2 {
    font-size: 13px;
    margin-left: -120px;
  }
  .linkToSignUpDiv {
    margin-left: -20px;
  }
  .wrapper2 .btn {
    height: 30px;
    width: 150px;
    margin-top: 20px;
  }
}
